"use client"

import React, { useEffect, useState } from "react"
import { Table, TableHeader, TableBody, TableHead, TableRow, TableCell } from "@/components/ui/table"
import { createClient } from "@/lib/supabase/client"
import type { DailyRecord } from "@/lib/types"

const ALL_COLUMNS = [
	{ key: "record_date", label: "Date" },
	{ key: "staff", label: "Staff" },
	{ key: "new_clients", label: "New Clients" },
	{ key: "today_trust_love", label: "Trust/Love" },
	{ key: "today_hot_chat", label: "Hot Chat" },
	{ key: "today_test_size_cut", label: "Test Size Cut" },
	{ key: "today_size_cut", label: "Size Cut" },
	{ key: "today_new_free_task", label: "New Free Task" },
	{ key: "today_promote_topup", label: "Promote Top-Up" },
	{ key: "today_promote_success", label: "Promote Success" },
	{ key: "today_new_interesting_clients", label: "New Interesting Clients" },
	{ key: "today_register", label: "Register" },
	{ key: "today_send_voice", label: "Send Voice" },
	{ key: "today_voice_call", label: "Voice Call" },
	{ key: "today_video_call", label: "Video Call" },
	{ key: "first_recharge_amount", label: "First Recharge" },
	{ key: "today_topup_amount", label: "Top-Up Amount" },
	{ key: "client_withdraw_amount", label: "Withdraw Amount" },
]

const ReportingPage = () => {
	const [records, setRecords] = useState<DailyRecord[]>([])
	const [loading, setLoading] = useState(true)
	const [error, setError] = useState<string | null>(null)
	const [selectedColumns, setSelectedColumns] = useState(ALL_COLUMNS.map(col => col.key))

	useEffect(() => {
		const fetchData = async () => {
			setLoading(true)
			setError(null)
			try {
				const supabase = createClient()
				const { data, error } = await supabase
					.from("daily_records")
					.select("*, staff(name)")
					.order("record_date", { ascending: false })
				if (error) throw error
				setRecords(data || [])
			} catch (err: any) {
				setError(err.message || "Failed to fetch data")
			} finally {
				setLoading(false)
			}
		}
		fetchData()
	}, [])

	const handleColumnToggle = (key: string) => {
		setSelectedColumns(cols =>
			cols.includes(key) ? cols.filter(col => col !== key) : [...cols, key]
		)
	}

	const columnsToShow = ALL_COLUMNS.filter(col => selectedColumns.includes(col.key))

	return (
		<main className="p-4 sm:p-8">
			<h1 className="text-2xl font-bold mb-4">Reporting</h1>
			<div className="mb-4 flex flex-wrap gap-2">
				{ALL_COLUMNS.map(col => (
					<label key={col.key} className="flex items-center gap-1 text-sm">
						<input
							type="checkbox"
							checked={selectedColumns.includes(col.key)}
							onChange={() => handleColumnToggle(col.key)}
							className="accent-primary h-4 w-4"
						/>
						{col.label}
					</label>
				))}
			</div>
			<div className="bg-white rounded shadow p-2 sm:p-4 overflow-x-auto">
				{loading ? (
					<p>Loading report data...</p>
				) : error ? (
					<p className="text-red-600">{error}</p>
				) : (
					<div className="min-w-[700px]">
						<Table className="min-w-full text-xs sm:text-sm">
							<TableHeader>
								<TableRow>
									{columnsToShow.map((col) => (
										<TableHead key={col.key} className="whitespace-nowrap px-2 py-2">{col.label}</TableHead>
									))}
								</TableRow>
							</TableHeader>
							<TableBody>
								{records.map((record) => (
									<TableRow key={record.id}>
										{columnsToShow.map(col => (
											<TableCell key={col.key} className="whitespace-nowrap px-2 py-2">
												{col.key === "staff"
													? record.staff?.name || "-"
													: (record as any)[col.key]}
											</TableCell>
										))}
									</TableRow>
								))}
							</TableBody>
						</Table>
					</div>
				)}
			</div>
		</main>
	)
}

export default ReportingPage
